export default {
	data() {
		return {
			indicatorDots: true, //指示点
			autoplay: true, //自动播放
			duration: 500, //幻灯片时长
			interval: 5000, //自动播放时长
		}
	},
	methods: {
		//指示点
		changeIndicatorDots (e) {
			this.indicatorDots = e.detail.value;
		},
		//自动播放
		changeAutoplay (e) {
			this.autoplay = e.detail.value;
		},
		//幻灯片时长
		durationChange (e) {
			this.duration = e.detail.value;
		},
		//自动播放时长
		intervalChange (e) {
			this.interval = e.detail.value;
		},
	}
}
